import React from 'react'

const VideoTitle = ({title,overview}) => {
  return (
    <div className='w-screen aspect-video pt-[17%] px-6 md:px-12 absolute text-white bg-gradient-to-r from-black'>
     <h1  className='text-2xl md:text-4xl  font-bold'>{title}</h1>
     <p className='hidden md:inline-block py-6 text-sm w-1/4'>{overview}</p>
     <div className='my-4 md:m-0'>
        <button className="bg-white text-black py-1 md:py-4 px-3 md:px-12 text-xl hover:opacity-50 rounded-lg"> Play</button>
        <button className="hidden md:inline-block mx-2 my-2  bg-gray-500 text-white p-3 px-12 text-xl bg-opacity-80 rounded-lg">More info</button>
     </div>
    </div>
  )
}

export default VideoTitle